.header {
  background-repeat: no-repeat;
  background-position: center;
  /* background-size: cover; */
  background-size: contain;
  background-color: #282c34;
  height: 100vh;
  max-height: 100%;
  width: 100%;
}
.header::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0,0,0,.4);
}